import React from 'react';
import { Contacto, FormContainer, Titulo, SubTitulo, Formulario, Imagen, Label, Input, Button } from './FormElements';
import { RectanguloGris } from '../product/ProductElements';
import ContactImg from '../../images/contact-image.png';

const Index = () => {
  return (
    <>
      <Contacto id="contact">
      <RectanguloGris/>
        <FormContainer>
          <Formulario action="http://sd-1059464-h00024.ferozo.net/backend/" method="POST">
            <Titulo>Get in touch</Titulo>
            <SubTitulo>We are hiring!</SubTitulo>
            <Label>Name</Label>
            <Input name="asunto"></Input>
            <Label>Email</Label>
            <Input name="email"></Input>
            <Label>Phone</Label>
            <Input name="telefono"></Input>
            <Label>Message</Label>
            <Input name="mensaje"></Input>
            <Button type="submit">Send</Button>
          </Formulario>
        </FormContainer>
        <Imagen>
          <img src={ContactImg} alt='Logo'/>
        </Imagen>
      </Contacto>
    </>
  )
}

export default Index